import React from 'react';
import image from './images/card/aga1.jpg';
import Footer from './footer';
import Navbar from './Navbar';
import a from './images/card/about.jpg';
import Card1 from './card3';

const AboutUs = () => {
    return (
        <> < Navbar /> <section id="ab" className="responsive-section">
  <img src={a} alt="About Us" className="responsive-image" />
  <div className="text-overlay" >
    <p className="pt">Jestemy do Twojej dyspozycji od 2014 roku</p>
  </div>
</section>
        <section className="containerA ">
            <h2
                style={{
                    textAlign: 'center',
                    margin: '0 auto',
                    paddingTop: '30px',
                    paddingBottom: '40px',
                    color:'#ddd',
                    fontWeight: 'semiBold',
                }}>O nas</h2>
            <div className="row clearfix" style={{paddingBottom: '30px'}}>
                <div className="col-md-4">
                    <img src={image} alt="About Us" className="img-fluid float-right" id='i'/>
                </div>
                <div className="col-md-8">
                    <p>Witamy w naszej firmie! Mam na imi Agnieszka i jestem dumnym czonkiem
                        naszego zespou. Nasza firma jest zaangaowana w pomoc ludziom w znalezieniu
                        idealnego domu, ktry odpowiada ich potrzebom i stylowi ycia.</p>
                    <p>W WEST24 rozumiemy, e zakup domu moe by trudnym zadaniem. Dlatego jestemy
                        tutaj, aby poprowadzi Ci przez cay proces, od poszukiwania wymarzonego domu
                        po negocjowanie najlepszej dla Ciebie oferty.</p>
                    <p>Mamy zesp dowiadczonych i oddanych profesjonalistw, ktrzy posiadaj
                        dogbn wiedz na temat brany nieruchomoci. Nasz zesp z pasj zapewnia
                        naszym klientom najlepsz moliw obsug. Wierzymy w budowanie dugotrwaych
                        relacji z naszymi klientami opartych na zaufaniu i wzajemnym szacunku.</p>
                    <p>Z dum oferujemy szereg nieruchomoci, ktre zaspokajaj rne budety i
                        potrzeby. Niezalenie od tego, czy szukasz przytulnego apartamentu, czy
                        przestronnej willi, mamy co, co speni Twoje wymagania. Nasze nieruchomoci
                        znajduj si w najbardziej podanych lokalizacjach i s zbudowane zgodnie z
                        najwyszymi standardami.</p>
                    <p>W WEST24 naszym celem jest, aby proces zakupu nieruchomoci by jak
                        najbardziej pynny i bezproblemowy. Dymy do zapewnienia wiatowej klasy usug
                        naszym klientom i staramy si przekracza ich oczekiwania.
                    </p>
                    <p>Dzikujemy za rozwaenie naszej firmy w celu zaspokojenia potrzeb zwizanych
                        z nieruchomociami. Z niecierpliwoci czekamy, aby by czci Twojej podry
                        do posiadania wymarzonego domu.</p>
                </div>
            </div>
       <Card1/>
       
       
        </section>



         < Footer /> </>
    );
}

export default AboutUs;
