import React, { useEffect } from 'react';
import Footer from './footer';
import HeroSlider from './imageSection';
import Navbar from './Navbar';
import ImageCard from './ResponsiveCard';
import Card1 from './card2';
import AOS from 'aos'
import 'aos/dist/aos.css';

function Home() {
    useEffect(() => {
        AOS.init({
            duration: 1000,
            easing: 'ease-in-out',
            once: true,
        });
    }, []);

    return (
        <>
            <Navbar />
            <div>
                <HeroSlider />

                <section className="box1" data-aos="fade-up">
                    <div className="container1">
                        <div className="box">
                            <h2 style={{ textAlign: 'center', color: '#244563' }}>Witamy</h2>
                            <p style={{ paddingLeft: 10, paddingRight: 10 }}>Witamy w West24 Real Estate Services! Ciesz si, e zdecydowae si odwiedzi moj stron internetow i dowiedzie si wicej o tym, jak mog pomc Ci kupi lub sprzeda dom. Dziki wieloletniemu dowiadczeniu w brany nieruchomoci posiadam wiedz i dowiadczenie, aby Twoja nastpna transakcja nieruchomoci zakoczya si sukcesem. Jestem dumny z zapewniania spersonalizowanej obsugi i uwagi kademu klientowi.</p>
                        </div>
                        <div className="box">
                            <h2 style={{ textAlign: 'center', color: '#244563' }}>Misj</h2>
                            <p style={{ paddingLeft: 10, paddingRight: 10 }}>Nasz misj jest umoliwienie kupujcym i sprzedajcym domw korzystania z porad ekspertw, innowacyjnych narzdzi i wyjtkowej obsugi, czc ich z oddanymi agentami nieruchomoci, aby uatwi pynne i satysfakcjonujce transakcje dotyczce nieruchomoci. Ta misja podkrela zaangaowanie firmy w zadowolenie klientw!</p>
                        </div>
                        <div className="box">
                            <h2 style={{ textAlign: 'center', color: '#244563' }}>Kim jestemy</h2>
                            <p style={{ paddingLeft: 10, paddingRight: 10 }}>Jestemy zespoem oddanych i owiadczonych profesjonalistw z brany nieruchomoci, ktrzy z pasj pomagaj naszym klientom osign ich cele zwizane z nieruchomociami. Dziki wieloletniemu dowiadczeniu w brany posiadamy wiedz i dowiadczenie, aby poprowadzi Ci przez kady etap procesu kupna lub sprzeday.</p>
                        </div>
                    </div>
                </section>

                <section style={{ backgroundColor: '#000028' }} data-aos="fade-up">
                    <h5 style={{ textAlign: 'center',color: '#ddd' }}>Ekspert ds. nieruchomoci w Wojewdztwie Dolnolskim</h5>
                    <ImageCard />
                </section>

                <section class="testimonials" data-aos="fade-up">
                    <h2 style={{ color: '#000000', paddingBottom: '10px' }}>Co powiedzieli nasi szanowni klienci:</h2>
                    <div class="testimonial-card">
                        <blockquote>
                            "Agnieszka bya przyjazna, profesjonalna i mia. Odpowiadaa na zapytania w odpowiednim czasie i polecibym jej zesp kademu, kto chce kupi lub sprzeda nieruchomo. Zdecydowanie 5-gwiazdkowe dowiadczenie."
        </blockquote>
                        <p class="author">Amanuel</p>
                    </div>
                    <div class="testimonial-card">
                        <blockquote>
                            "wietny agent! Dzikuj Agnieszko za pomoc w znalezieniu domu na zawsze."
        </blockquote>
                        <p class="author">Iwona</p>
                    </div>

                </section>

                <section style={{ backgroundColor: ' #000020' }} data-aos="fade-up">
                    <Card1 />
                </section>

               
            </div>
            <Footer />
        </>
    );
}

export default Home;