
import React, { useState } from 'react';
import { Form, Button } from 'react-bootstrap';
import axios from 'axios';

const ContactForm = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [mobile, setMobile] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();

    axios.post('/api/send-email', {
      name,
      email,
      mobile,
      message
    })
      .then((response) => {
        console.log(response);
        alert('Email sent successfully');
        setName('');
        setEmail('');
        setMobile('');
        setMessage('');
      })
      .catch((error) => {
        console.log(error);
        alert('Error sending email');
      });
  };

  return (
    <div className="p-4 bg-white border rounded">
      <h2 className="text-center">Contact Us</h2>
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="fullName">
          <Form.Label>Nazwa:</Form.Label>
          <Form.Control type="text" value={name} onChange={(e) => setName(e.target.value)} required />
        </Form.Group>
        <Form.Group controlId="email">
          <Form.Label>Adres e-mail:</Form.Label>
          <Form.Control type="email" value={email} onChange={(e) => setEmail(e.target.value)} required />
        </Form.Group>
        <Form.Group controlId="mobile">
          <Form.Label>Numer telefonu:</Form.Label>
          <Form.Control type="text" value={mobile} onChange={(e) => setMobile(e.target.value)} required />
        </Form.Group>
        <Form.Group controlId="message">
          <Form.Label>Wiadomo:</Form.Label>
          <Form.Control as="textarea" rows={3} value={message} onChange={(e) => setMessage(e.target.value)} required />
        </Form.Group>
        <Button variant="primary" type="submit" style={{marginTop:'24px'}}>
          Wylij
        </Button>
      </Form>
    </div>
  );
};

export default ContactForm;