import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Footer from './footer';
import Navbar from './Navbar';

// Import images
import Kin from './images/card/wd.jpg';
import W3 from './images/card/w30.webp';
import W4 from './images/card/w5.jpg';
import W5 from './images/card/w6.jpg';
import W6 from './images/card/w7.webp';
import W8 from './images/card/w8.webp';
import W16 from './images/card/w16.jpg';
import W17 from './images/card/w17.jpg';
import W18 from './images/card/w18.jpg';
import W19 from './images/card/w19.jpg';
import W20 from './images/card/w20.jpg';
import W21 from './images/card/w9.webp';
import W22 from './images/card/W26.jpg';
import W23 from './images/card/w30.jpg';
import W24 from './images/card/w28.jpg';

export default function Card1() {
    const [items, setItems] = useState([
        {
            id: 1,
            name: 'Dom Kryniczno',
            price: '849 000 z',
            size: '100 m',
            pricePerSize: '8 490 z/m',
            image: Kin,
            link: '/DomKryniczno'
        },
        {
            id: 2,
            name: 'Mieszkanie Wrocaw Krzyki',
            price: '895 300 z',
            size: '70 m',
            pricePerSize: '12 790 z/m',
            image: W3,
            link: '/Krzyki'
        },
        {
            id: 3,
            name: 'Dom Trzebnica',
            price: '449 000 z',
            size: '100 m',
            pricePerSize: '4 490 z/m',
            image: W4,
            link: '/Trzebnica'
        },
        {
            id: 4,
            name: 'Dom ul. Milicka',
            price: '850 000 z',
            size: '160 m',
            pricePerSize: '5 312,50 z/m',
            image: W5,
            link: '/Milicka'
        },
        {
            id: 5,
            name: 'Mieszkanie ul. ernicka',
            price: '559 000 z',
            size: '40,40 m',
            pricePerSize: '13 836,63 z/m',
            image: W8,
            link: '/ernicka'
        },
        {
            id: 6,
            name: 'Dziaka budowlana ul. Bkitna',
            price: '160 000 z',
            size: '1 500 m',
            pricePerSize: '106,67 z/m',
            image: W6,
            link: '/Bkitna'
        },
        {
            id: 7,
            name: 'Mieszkanie ul. Potokowa',
            price: '885 000 z',
            size: '68,30 m',
            pricePerSize: '12 957,54 z/m',
            image: W16,
            link: '/Potokowa'
        },
        {
            id: 8,
            name: 'Dziaka rolna cinawa',
            price: '56 160 z',
            size: '14 400 m',
            pricePerSize: '3,90 z/m',
            image: W17,
            link: '/Kryniczno'
        },
        {
            id: 9,
            name: 'Dziaka budowlana ul. Wrocawska',
            price: '500 000 z',
            size: '1 800 m',
            pricePerSize: '277,78 z/m',
            image: W18,
            link: '/Wrocawska'
        },
        {
            id: 10,
            name: 'Mieszkanie ul. Potokowa',
            price: '2 950 000 z',
            size: '320 m',
            pricePerSize: '9 218,75 z/m',
            image: W19,
            link: '/Potokowa2'
        },
        {
            id: 11,
            name: 'Dziaka ul. Ceglana',
            price: '4 385 100 z',
            size: '14 617 m',
            pricePerSize: '300 z/m',
            image: W20,
            link: '/Ceglana'
        },
        {
            id: 12,
            name: 'Biuro ul. Ofiar Owicimskich',
            price: '11 550 z',
            size: '165 m',
            pricePerSize: '70 z/m',
            image: W21,
            link: '/OfiarOwicimskich'
        },
        {
            id: 13,
            name: 'Dom Wrocaw Karowice',
            price: '2 100 000 z',
            size: '250 m',
            pricePerSize: '8 400 z/m',
            image: W22,
            link: '/KarowiceWrocaw'
        },
        {
            id: 14,
            name: 'Mieszkanie ul. Potokowa',
            price: '649 999 z',
            size: '46,78 m',
            pricePerSize: '13 894,81 z/m',
            image: W23,
            link: '/Potokowa3'
        },
        {
            id: 15,
            name: 'Dom Wrocaw Krzyki',
            price: '2 290 000 z',
            size: '280 m',
            pricePerSize: '8 178,57 z/m',
            image: W24,
            link: '/KrzykiWrocaw2'
        }
    ]);

    return (
        <>
            <Navbar />
            <section className="box1" id='box1' style={{ backgroundColor: '#000020' }}>
                <h3 style={{ color: '#ddd', textAlign: 'center', paddingTop: '50px' }}>Witamy na naszej stronie z ofertami nieruchomoci!</h3>
                <p style={{ color: '#ddd', textAlign: 'center' }}>Mamy wiele piknych domw na sprzeda w najlepszych lokalizacjach.</p>
                <div className="container1" style={{ paddingTop: '50px' }}>
                    {items.map(item => (
                        <div className="box6" key={item.id}>
                            <Link to={item.link} style={{ textDecoration: 'none' }} onClick={() => window.scrollTo(0, 0)}>
                                <img src={item.image} alt={item.name} className="d-block" style={{ width: '100%' }} />
                                <p>{item.name}<br />
                                    <span style={{ marginLeft: '10px', color: 'red' }}>{item.price}</span>
                                    <span style={{ marginLeft: '10px', color: 'blue' }}>{item.size}</span>
                                    <span style={{ marginLeft: '10px', color: 'green' }}>{item.pricePerSize}</span>
                                </p>
                            </Link>
                        </div>
                    ))}
                </div>
            </section>
            <Footer />
        </>
    );
}
