import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Navbar from './pages/Navbar';
import Footer from './pages/footer';

// Sample images for demonstration
import W4 from './pages/images/card/w5.jpg';
import Kin from './pages/images/card/wd.jpg';
import W5 from './pages/images/card/w6.jpg';
import W6 from './pages/images/card/w7.webp';
import W8 from './pages/images/card/w8.webp';
import W16 from './pages/images/card/w16.jpg';
import W17 from './pages/images/card/w17.jpg';
import W18 from './pages/images/card/w18.jpg';
import W19 from './pages/images/card/w19.jpg';
import W20 from './pages/images/card/w20.jpg';
import W21 from './pages/images/card/w9.webp';
import W22 from './pages/images/card/W26.jpg';
import W23 from './pages/images/card/w30.jpg';
import W24 from './pages/images/card/w28.jpg';
import W3 from './pages/images/card/w30.webp';

const AdminPage = () => {
    const [items, setItems] = useState([
        { id: 1, name: 'Dom Kryniczno', price: '849 000 z', size: '100 m', pricePerSize: '8 490 z/m', image: Kin, link: '/DomKryniczno' },
        { id: 2, name: 'Mieszkanie Wrocaw Krzyki', price: '895 300 z', size: '70 m', pricePerSize: '12 790 z/m', image: W3, link: '/Krzyki' },
        { id: 3, name: 'Dom Trzebnica', price: '449 000 z', size: '100 m', pricePerSize: '4 490 z/m', image: W4, link: '/Trzebnica' },
        { id: 4, name: 'Dom ul. Milicka', price: '850 000 z', size: '160 m', pricePerSize: '5 312,50 z/m', image: W5, link: '/Milicka' },
        { id: 5, name: 'Mieszkanie ul. ernicka', price: '559 000 z', size: '40,40 m', pricePerSize: '13 836,63 z/m', image: W8, link: '/ernicka' },
        { id: 6, name: 'Dziaka budowlana ul. Bkitna', price: '160 000 z', size: '1 500 m', pricePerSize: '106,67 z/m', image: W6, link: '/Bkitna' },
        { id: 7, name: 'Mieszkanie ul. Potokowa', price: '885 000 z', size: '68,30 m', pricePerSize: '12 957,54 z/m', image: W16, link: '/Potokowa' },
        { id: 8, name: 'Dziaka rolna cinawa', price: '56 160 z', size: '14 400 m', pricePerSize: '3,90 z/m', image: W17, link: '/Kryniczno' },
        { id: 9, name: 'Dziaka budowlana ul. Wrocawska', price: '500 000 z', size: '1 800 m', pricePerSize: '277,78 z/m', image: W18, link: '/Wrocawska' },
        { id: 10, name: 'Mieszkanie ul. Potokowa', price: '2 950 000 z', size: '320 m', pricePerSize: '9 218,75 z/m', image: W19, link: '/Potokowa2' },
        { id: 11, name: 'Dziaka ul. Ceglana', price: '4 385 100 z', size: '14 617 m', pricePerSize: '300 z/m', image: W20, link: '/Ceglana' },
        { id: 12, name: 'Biuro ul. Ofiar Owicimskich', price: '11 550 z', size: '165 m', pricePerSize: '70 z/m', image: W21, link: '/OfiarOwicimskich' },
        { id: 13, name: 'Dom Wrocaw Karowice', price: '2 100 000 z', size: '250 m', pricePerSize: '8 400 z/m', image: W22, link: '/KarowiceWrocaw' },
        { id: 14, name: 'Mieszkanie ul. Potokowa', price: '649 999 z', size: '46,78 m', pricePerSize: '13 894,81 z/m', image: W23, link: '/Potokowa3' },
        { id: 15, name: 'Dom Wrocaw Krzyki', price: '2 290 000 z', size: '280 m', pricePerSize: '8 178,57 z/m', image: W24, link: '/KrzykiWrocaw2' }
    ]);

    const [form, setForm] = useState({ name: '', price: '', size: '', pricePerSize: '', image: '', link: '' });

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setForm({ ...form, [name]: value });
    };

    const handleAddItem = () => {
        setItems([...items, { ...form, id: items.length + 1 }]);
        setForm({ name: '', price: '', size: '', pricePerSize: '', image: '', link: '' });
    };

    const handleDeleteItem = (id) => {
        setItems(items.filter(item => item.id !== id));
    };

    const handleUpdateItem = (id) => {
        const updatedItems = items.map(item => item.id === id ? { ...item, ...form } : item);
        setItems(updatedItems);
        setForm({ name: '', price: '', size: '', pricePerSize: '', image: '', link: '' });
    };

    return (
        <>
            <Navbar />
            <section style={{ backgroundColor: '#f0f0f0', padding: '20px' }}>
                <h2>Admin Page</h2>
                <div>
                    <h3>Add New Item</h3>
                    <input type="text" name="name" placeholder="Name" value={form.name} onChange={handleInputChange} />
                    <input type="text" name="price" placeholder="Price" value={form.price} onChange={handleInputChange} />
                    <input type="text" name="size" placeholder="Size" value={form.size} onChange={handleInputChange} />
                    <input type="text" name="pricePerSize" placeholder="Price Per Size" value={form.pricePerSize} onChange={handleInputChange} />
                    <input type="text" name="image" placeholder="Image Path" value={form.image} onChange={handleInputChange} />
                    <input type="text" name="link" placeholder="Link" value={form.link} onChange={handleInputChange} />
                    <button onClick={handleAddItem}>Add Item</button>
                </div>
                <div>
                    <h3>Existing Items</h3>
                    {items.map(item => (
                        <div key={item.id} style={{ marginBottom: '20px', padding: '10px', border: '1px solid #ccc' }}>
                            <p>{item.name} - {item.price} - {item.size} - {item.pricePerSize}</p>
                            <img src={item.image} alt={item.name} style={{ width: '100px' }} />
                            <div>
                                <button onClick={() => handleDeleteItem(item.id)}>Delete</button>
                                <button onClick={() => handleUpdateItem(item.id)}>Update</button>
                            </div>
                        </div>
                    ))}
                </div>
            </section>
            <Footer />
        </>
    );
};

export default AdminPage;
