import React from 'react';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import Navbar from './Navbar';
import Footer from './footer';
import { Link } from 'react-router-dom';
import ContactForm from './ContactForm';
import ContactInfo from './ContactInfo';
import GoogleMap from './GoogleMap';

const ContactUs = () => {
  return (
    <>
      <Navbar />
      <div className="pt-100">
        <h4 className="text-center" style={{color: '#ddd'}}>Skontaktuj si z nami</h4>
        <p className="px-60"  style={{color: '#ddd'}}>
          Dzikujemy za odwiedzenie naszej strony internetowej! Jestemy tutaj, aby pomc we wszystkich potrzebach zwizanych z nieruchomociami. Wypenij poniszy formularz lub skontaktuj si z nami telefonicznie lub mailowo. Czekamy na wiadomo od Ciebie!
        </p>
        <Container className="mt-5">
          <Row>
            <Col md={6}>
              <ContactForm />
            </Col>
            <Col md={6}>
              <ContactInfo />
            </Col>
          </Row>
        </Container>
        <GoogleMap />
      </div>
      <Footer />
    </>
  );
};

export default ContactUs;